import React from 'react'
import {connect} from 'react-redux'
import withStyles from 'isomorphic-style-loader/lib/withStyles'
import {Button, Form, Input, Modal, Switch} from 'antd'
import s from './AddCurrencyModal.css'
import {addCurrency, closeAddCurrencyModal} from '../../reducers/settings'
import {withCurrency} from '../../components'

class AddCurrencyModal extends React.Component {
  state = {
    symbol: '',
    prefix: false,
  }

  handleSubmit = (e) => {
    e.preventDefault()
    this.props.form.validateFields((err, values) => {
      if (!err) {
        this.props.addCurrency(values)
        this.props.form.resetFields()
      }
    })
  }

  handleChangeSymbol = (e) => {
    this.setState({symbol: e.target.value})
  }

  handleChangePrefix = (prefix) => {
    this.setState({prefix})
  }

  render() {
    const {closeAddCurrencyModal} = this.props
    const {getFieldDecorator} = this.props.form
    const {symbol, prefix} = this.state
    const formItemLayout = {
      labelCol: {
        xs: {span: 8},
      },
      wrapperCol: {
        xs: {span: 16},
      },
    }
    return (
      <Modal
        width={300}
        visible
        title={'Add Currency'}
        onCancel={closeAddCurrencyModal}
        footer={
          <React.Fragment>
            <Button key='cancel' onClick={closeAddCurrencyModal}>Cancel</Button>
            <Button key='ok' type='primary' onClick={this.handleSubmit}>
              OK
            </Button>
          </React.Fragment>
        }
      >
        <div className={s.preview}>
          {symbol ? withCurrency({symbol: [prefix ? symbol : '', prefix ? '' : symbol]}, 100) : 'Preview here'}
        </div>
        <Form layout='vertical' hideRequiredMark>
          <Form.Item
            label={'Name'}
            {...formItemLayout}
            className={s.row}
          >
            {getFieldDecorator('label', {
              rules: [
                {required: true, message: 'This field is required.', whitespace: true}
              ],
            })(
              <Input size='small'/>
            )}
          </Form.Item>
          <Form.Item
            label={'Symbol'}
            {...formItemLayout}
            className={s.row}
          >
            {getFieldDecorator('symbol', {
              rules: [
                {required: true, message: 'This field is required.', whitespace: true}
              ],
            })(
              <Input size='small' onChange={this.handleChangeSymbol}/>
            )}
          </Form.Item>
          <Form.Item
            label={'As prefix'}
            {...formItemLayout}
            className={s.row}
          >
            {getFieldDecorator('prefix', {})(
              <Switch onChange={this.handleChangePrefix}/>
            )}
          </Form.Item>
        </Form>
      </Modal>
    )

  }
}

const mapState = state => ({})

const mapDispatch = {
  closeAddCurrencyModal,
  addCurrency,
}

export default connect(mapState, mapDispatch)(Form.create()(withStyles(s)(AddCurrencyModal)))



