import React from 'react'

const withCurrency = (currency, content) => {
  const prefix = currency.symbol[0]
  const suffix = currency.symbol[1]
  return (
    <div style={{display: 'flex'}}>
      {prefix}
      {content}
      {' '}{suffix}
    </div>
  )
}

export default withCurrency




