import createReducer from '../createReducer'
import {CURRENCIES, NEW_CURRENCY_OPTION} from '../constants'

// ------------------------------------
// Constants
// ------------------------------------
export const SET_TAX_VISIBLE = 'Settings.SET_TAX_VISIBLE'
export const CHANGE_TAX = 'Settings.CHANGE_TAX'
export const CHANGE_DATE_FORMAT = 'Settings.CHANGE_DATE_FORMAT'
export const SET_DEFAULT_TODAY = 'Settings.SET_DEFAULT_TODAY'

export const CHANGE_CURRENCY = 'Settings.CHANGE_CURRENCY'
export const ADD_CURRENCY = 'Settings.ADD_CURRENCY'
export const REMOVE_CURRENCY = 'Settings.REMOVE_CURRENCY'

export const OPEN_ADD_CURRENCY_MODAL = 'Settings.OPEN_ADD_CURRENCY_MODAL'
export const CLOSE_ADD_CURRENCY_MODAL = 'Settings.CLOSE_ADD_CURRENCY_MODAL'

export const OPEN_SIDEBAR = 'Settings.OPEN_SIDEBAR'
export const CLOSE_SIDEBAR = 'Settings.CLOSE_SIDEBAR'

// ------------------------------------
// Actions
// ------------------------------------
export const setTaxVisible = (taxVisible) => ({type: SET_TAX_VISIBLE, taxVisible})

export const changeTax = (tax) => ({type: CHANGE_TAX, tax})

export const changeDateFormat = (dateFormat) => ({type: CHANGE_DATE_FORMAT, dateFormat})

export const setDefaultToday = (defaultToday) => ({type: SET_DEFAULT_TODAY, defaultToday})

export const changeCurrency = (key) => (dispatch, getState) => {
  const {currencies} = getState().settings
  if (key === NEW_CURRENCY_OPTION) {
    dispatch(openAddCurrencyModal())
  } else {
    const currency = [...currencies, ...CURRENCIES].find(item => item.key === key)
    dispatch({type: CHANGE_CURRENCY, currency})
  }
}

export const addCurrency = ({label, prefix, symbol}) => (dispatch, getState) => {
  const {currencies} = getState().settings
  const currency = {
    label,
    key: Math.random(),
    // TODO move to utils
    symbol: [prefix ? symbol : '', prefix ? '' : symbol],
    custom: true,
  }
  dispatch(closeAddCurrencyModal())
  dispatch({type: ADD_CURRENCY, currencies: [currency, ...currencies], currency})
}

export const removeCurrency = (currencyToRemove) => (dispatch, getState) => {
  let {currencies, currency} = getState().settings
  const updatedCurrencies = currencies.filter(item => item.key !== currencyToRemove.key)
  if (currency.key === currencyToRemove.key) {
    currency = [...updatedCurrencies, ...CURRENCIES][0]
  }
  dispatch({
    type: REMOVE_CURRENCY,
    currencies: updatedCurrencies,
    currency,
  })
}

export const openAddCurrencyModal = () => ({type: OPEN_ADD_CURRENCY_MODAL})

export const closeAddCurrencyModal = () => ({type: CLOSE_ADD_CURRENCY_MODAL})

export const openSidebar = () => ({type: OPEN_SIDEBAR})

export const closeSidebar = () => ({type: CLOSE_SIDEBAR})

// ------------------------------------
// Reducer
// ------------------------------------
const initialState = {
  taxVisible: true,
  tax: '',
  dateFormat: 'DD/MM/YYYY',
  defaultToday: true,
  currency: {
    label: 'Euro',
    key: 'EUR',
    symbol: ['€', ''],
  },
  currencies: [],
  clients: [],
  addCurrencyModalOpened: false,
  sidebarOpened: false,
}

export default createReducer(initialState, {
  [SET_TAX_VISIBLE]: (state, {taxVisible}) => ({
    taxVisible,
  }),
  [CHANGE_TAX]: (state, {tax}) => ({
    tax,
  }),
  [CHANGE_DATE_FORMAT]: (state, {dateFormat}) => ({
    dateFormat,
  }),
  [SET_DEFAULT_TODAY]: (state, {defaultToday}) => ({
    defaultToday,
  }),
  [CHANGE_CURRENCY]: (state, {currency}) => ({
    currency,
  }),
  [ADD_CURRENCY]: (state, {currencies, currency}) => ({
    currencies,
    currency,
  }),
  [REMOVE_CURRENCY]: (state, {currencies, currency}) => ({
    currencies,
    currency,
  }),
  [OPEN_ADD_CURRENCY_MODAL]: (state, action) => ({
    addCurrencyModalOpened: true,
  }),
  [CLOSE_ADD_CURRENCY_MODAL]: (state, action) => ({
    addCurrencyModalOpened: false,
  }),
  [OPEN_SIDEBAR]: (state, action) => ({
    sidebarOpened: true,
  }),
  [CLOSE_SIDEBAR]: (state, action) => ({
    sidebarOpened: false,
  }),
})



