import React from 'react'
import {connect} from 'react-redux'
import withStyles from 'isomorphic-style-loader/lib/withStyles'
import {Button, Form, Input, Modal} from 'antd'
import s from './AddClientModal.css'
import {addClient, closeAddClientModal} from '../../reducers/client'

class AddClientModal extends React.Component {
  handleSubmit = (e) => {
    e.preventDefault()
    this.props.form.validateFields((err, values) => {
      if (!err) {
        this.props.addClient(values)
      }
    })
  }

  render() {
    const {closeAddClientModal} = this.props
    const {getFieldDecorator} = this.props.form
    const formItemLayout = {
      labelCol: {
        xs: {span: 8},
      },
      wrapperCol: {
        xs: {span: 16},
      },
    }
    return (
      <Modal
        width={300}
        visible
        title={'Save Client'}
        onCancel={closeAddClientModal}
        footer={
          <React.Fragment>
            <Button key='cancel' onClick={closeAddClientModal}>Cancel</Button>
            <Button key='ok' type='primary' onClick={this.handleSubmit}>
              OK
            </Button>
          </React.Fragment>
        }
      >
        <Form layout='vertical' hideRequiredMark>
          <Form.Item
            label={'Name'}
            {...formItemLayout}
            className={s.row}
          >
            {getFieldDecorator('label', {
              rules: [
                {required: true, message: 'This field is required.', whitespace: true}
              ],
            })(
              <Input size='small'/>
            )}
          </Form.Item>
        </Form>
      </Modal>
    )

  }
}

const mapState = state => ({})

const mapDispatch = {
  closeAddClientModal,
  addClient,
}

export default connect(mapState, mapDispatch)(Form.create()(withStyles(s)(AddClientModal)))



