import React from 'react'

const PSEUDO_HIDDEN = {
  height: 0,
  overflow: 'hidden',
}

class Label extends React.Component {
  static defaultProps = {
    formatter: n => n,
    labelFormatter: n => n,
    precision: 0,
  }

  constructor(props) {
    super(props)
    this.state = {
      focused: false,
    }
  }

  onBlur = () => {
    this.setState({focused: false})
  }

  onFocus = (e) => {
    e.target.select()
    this.setState({focused: true})
  }


  edit = () => {
    this.input.inputNumberRef.focus()
    this.setState({focused: true})
  }

  render() {
    const {focused} = this.state
    const {className, formatter, labelFormatter, precision, value, ...otherProps} = this.props
    const Component = this.props.component
    return (
      <div className={className} onClick={this.edit}>
        <Component
          ref={(ref) => {
            this.input = ref
          }}
          formatter={formatter}
          precision={precision}
          value={value}
          {...otherProps}
          onBlur={this.onBlur}
          onFocus={this.onFocus}
          style={!focused ? PSEUDO_HIDDEN : {}}
        />
        {!focused && value && labelFormatter(formatter(value.toFixed(precision)))}
      </div>
    )
  }
}

export default Label




