import React from 'react'
import {connect} from 'react-redux'
import withStyles from 'isomorphic-style-loader/lib/withStyles'
import {Button, Icon, Layout} from 'antd'
import s from './Header.css'
import {closeSidebar, openSidebar} from '../../reducers/settings'

class Header extends React.Component {
  print = () => {
    if (typeof window !== 'undefined') {
      window.print()
    }
  }

  render() {
    const {sidebarOpened, openSidebar, closeSidebar} = this.props
    return (
      <Layout.Header className={s.header}>
        <Icon
          className={s.toggler}
          type={sidebarOpened ? 'menu-unfold' : 'menu-fold'}
          onClick={sidebarOpened ? closeSidebar : openSidebar}
        />
        <Button
          type='dashed'
          ghost
          className={s.printBtn}
          onClick={this.print}
        >
          Print
        </Button>
      </Layout.Header>
    )
  }
}

const mapState = state => ({
  sidebarOpened: state.settings.sidebarOpened,
})

const mapDispatch = {
  openSidebar,
  closeSidebar,
}

export default connect(mapState, mapDispatch)(withStyles(s)(Header))




