import React from 'react'
import {connect} from 'react-redux'
import withStyles from 'isomorphic-style-loader/lib/withStyles'
import {Input, Select, Switch, Row, Col, Icon} from 'antd'
import s from './Sidebar.css'
import {closeSidebar} from '../../reducers/settings'
import {changeCurrency, changeDateFormat, changeTax, setTaxVisible, setDefaultToday, removeCurrency} from '../../reducers/settings'
import Drawer from 'rc-drawer-menu'
import {CURRENCIES, NEW_CURRENCY_OPTION} from '../../constants'
import {AddCurrencyModal} from '../../components'

class Sidebar extends React.Component {
  render() {
    const {taxVisible, setTaxVisible, tax, changeTax, dateFormat, changeDateFormat, currency, currencies, changeCurrency, sidebarOpened, closeSidebar, defaultToday, setDefaultToday, removeCurrency, addCurrencyModalOpened} = this.props
    return (
      <Drawer
        wrapperClassName={s.sidebar}
        width='270px'
        open={sidebarOpened}
        iconChild={false}
        level={null}
        onMaskClick={closeSidebar}
      >
        <Row type='flex' justify='space-between' className={s.row}>
          <Col>
            Tax visible
          </Col>
          <Col>
            <Switch
              checked={taxVisible}
              onChange={setTaxVisible}
            />
          </Col>
        </Row>
        {taxVisible && (
          <Row type='flex' justify='space-between' className={s.row}>
            <Col>Tax value</Col>
            <Col>
              <Input
                size='small'
                id='tax'
                placeholder='Type % or text'
                className={s.tax}
                value={tax}
                onChange={e => changeTax(e.target.value)}
              />
            </Col>
          </Row>
        )}
        <Row type='flex' justify='space-between' className={s.row}>
          <Col>
            Today's date as default
          </Col>
          <Col>
            <Switch
              checked={defaultToday}
              onChange={setDefaultToday}
            />
          </Col>
        </Row>
        {defaultToday && (
          <Row type='flex' justify='space-between' className={s.row}>
            <Col>Date format</Col>
            <Col>
              <Input
                size='small'
                id='dateFormat'
                className={s.dateFormat}
                value={dateFormat}
                onChange={e => changeDateFormat(e.target.value)}
              />
            </Col>
          </Row>
        )}
        <Row type='flex' justify='space-between' className={s.row}>
          <Col>Currency</Col>
          <Col>
            <Select
              id='currency'
              className={s.currency}
              value={currency.key}
              onSelect={changeCurrency}
              optionLabelProp='children'
              size='small'
              showSearch
              filterOption={(input, option) => {
                if (option.key === NEW_CURRENCY_OPTION) {
                  return true
                }
                return option.props.children[0].props.children.toLowerCase().includes(input.toLowerCase())
              }}
            >
              <Select.Option key={NEW_CURRENCY_OPTION}>+ Add currency</Select.Option>
              {[...currencies, ...CURRENCIES].map(item =>
                <Select.Option key={item.key} value={item.key} className={s.currencyOption} title={item.label}>
                  <div className={s.currencyLabel}>{item.label}</div>
                  {item.custom && (
                    <Icon
                      type='delete'
                      className={s.removeCurrency}
                      title='Remove'
                      onClick={(e) => {
                        e.stopPropagation()
                        removeCurrency(item)
                      }}
                    />
                  )}
                </Select.Option>
              )}
            </Select>
            {addCurrencyModalOpened && <AddCurrencyModal/>}
          </Col>
        </Row>
        <a
          className={s.contactBtn}
          href='https://t.me/serhii_levchenko'
          target='_blank'
          ref='nofollow'
        >
          Contact me on Telegram
        </a>
      </Drawer>
    )
  }
}

const mapState = state => ({
  addCurrencyModalOpened: state.invoice.addCurrencyModalOpened,
  ...state.settings,
})

const mapDispatch = {
  setTaxVisible,
  changeTax,
  changeDateFormat,
  changeCurrency,
  closeSidebar,
  setDefaultToday,
  removeCurrency,
}

export default connect(mapState, mapDispatch)(withStyles(s)(Sidebar))




