import React from 'react'
import {connect} from 'react-redux'
import withStyles from 'isomorphic-style-loader/lib/withStyles'
import s from './Invoice.css'
import {Client, Company, Details, Items, TinyMCE} from '../../components'
import {Col, Form, Row, Spin} from 'antd'
import {SIMPLE_TINYMCE_CONFIG, TINYMCE_CONFIG} from '../../constants'
import cn from 'classnames'

class Invoice extends React.Component {
  state = {
    hasMounted: false,
  }

  componentDidMount() {
    this.setState({hasMounted: true})
  }

  render() {
    const {hasMounted} = this.state
    const {getFieldDecorator} = this.props.form

    return (
      <Spin wrapperClassName={s.container} spinning={!hasMounted}>
        <div className={s.content}>
          {hasMounted && (
            <React.Fragment>
              <Row type='flex' gutter={32}>
                <Col span='15'>
                  <Company/>
                </Col>
                <Col span='9'>
                  {getFieldDecorator('extra', {
                    initialValue: `Invoice`,
                  })(
                    <TinyMCE
                      className={cn(s.field, s.extra)}
                      tinymceConfig={SIMPLE_TINYMCE_CONFIG}
                      component='div'
                    />
                  )}
                </Col>
              </Row>
              <div className={s.details}>
                <Row type='flex' gutter={32} justify='space-between'>
                  <Col span='15'>
                    <Row type='flex' gutter={16}>
                      <Col span='4'>
                        {getFieldDecorator('billTo', {
                          initialValue: `Bill To`,
                        })(
                          <TinyMCE
                            className={cn(s.field, s.label)}
                            tinymceConfig={SIMPLE_TINYMCE_CONFIG}
                            component='div'
                          />
                        )}
                      </Col>
                      <Col span='20'>
                        <Client/>
                      </Col>
                    </Row>
                  </Col>
                  <Col span='9'>
                    <Details/>
                  </Col>
                </Row>
              </div>
              <Items/>
              <div className={s.notesWrapper}>
                {getFieldDecorator('notes', {
                  initialValue: ``
                })(
                  <TinyMCE
                    className={s.field}
                    tinymceConfig={TINYMCE_CONFIG}
                    component='div'
                  />
                )}
              </div>
            </React.Fragment>
          )}
        </div>
      </Spin>
    )
  }
}

const mapState = state => ({})

const mapDispatch = {}

export default connect(mapState, mapDispatch)(Form.create()(withStyles(s)(Invoice)))



