import React from 'react'
import {connect} from 'react-redux'
import withStyles from 'isomorphic-style-loader/lib/withStyles'
import s from './Client.css'
import {TinyMCE, AddClientModal} from '../../components'
import {Icon, Select, Tooltip} from 'antd'
import {TINYMCE_CONFIG} from '../../constants'
import isEmpty from 'lodash/isEmpty'
import {openAddClientModal, changeClient, removeClient, selectClient} from '../../reducers/client'

class Client extends React.Component {
  render() {
    const {openAddClientModal, clients, selectedClient, removeClient, changeClient, client, selectClient, addClientModalOpened} = this.props
    return (
      <div className={s.clientWrapper}>
        <TinyMCE
          className={s.field}
          tinymceConfig={TINYMCE_CONFIG}
          component='div'
          value={client}
          onChange={changeClient}
        />
        {!isEmpty(clients) && (
          <Select
            className={s.client}
            value={selectedClient && selectedClient.value}
            onChange={selectClient}
          >
            {clients.map(item =>
              <Select.Option key={item.value} className={s.clientOption}>
                <div className={s.clientLabel}>{item.label}</div>
                <Icon
                  type='delete'
                  className={s.removeClient}
                  title='Remove'
                  onClick={(e) => {
                    e.stopPropagation()
                    removeClient(item)
                  }}
                />
              </Select.Option>
            )}
          </Select>
        )}
        {(!selectedClient || client !== selectedClient.value) && (
          <Tooltip placement='right' title={'Save client'}>
            <Icon
              className={s.saveIcon}
              onClick={openAddClientModal}
              type='save'
            />
          </Tooltip>
        )}
        {addClientModalOpened && <AddClientModal/>}
      </div>
    )
  }
}

const mapState = state => ({
  ...state.client,
})

const mapDispatch = {
  openAddClientModal,
  changeClient,
  selectClient,
  removeClient,
}

export default connect(mapState, mapDispatch)(withStyles(s)(Client))




