import createReducer from '../createReducer'
import {DEFAULTS} from '../constants'

// ------------------------------------
// Constants
// ------------------------------------
export const CHANGE_COMPANY = 'Company.CHANGE_COMPANY'
export const SELECT_COMPANY = 'Company.SELECT_COMPANY'
export const ADD_COMPANY = 'Company.ADD_COMPANY'
export const REMOVE_COMPANY = 'Company.REMOVE_COMPANY'

export const OPEN_ADD_COMPANY_MODAL = 'Company.OPEN_ADD_COMPANY_MODAL'
export const CLOSE_ADD_COMPANY_MODAL = 'Company.CLOSE_ADD_COMPANY_MODAL'

// ------------------------------------
// Actions
// ------------------------------------
export const changeCompany = (company) => ({type: CHANGE_COMPANY, company})

export const addCompany = ({label}) => (dispatch, getState) => {
  const {companies, company} = getState().company
  const newCompany = {value: company, label}
  dispatch(closeAddCompanyModal())
  dispatch({
    type: ADD_COMPANY,
    companies: [...companies, newCompany],
    selectedCompany: newCompany,
  })
}

export const removeCompany = (companyToRemove) => (dispatch, getState) => {
  let {companies, selectedCompany} = getState().company

  if (selectedCompany && selectedCompany.value === companyToRemove.value) {
    selectedCompany = null
  }

  dispatch({
    type: REMOVE_COMPANY,
    companies: companies.filter(item => item.value !== companyToRemove.value),
    selectedCompany,
  })
}

export const selectCompany = (company) => (dispatch, getState) => {
  const {companies} = getState().company
  const selectedCompany = companies.find(item => item.value === company)
  dispatch({type: SELECT_COMPANY, selectedCompany, company})
}

export const openAddCompanyModal = () => ({type: OPEN_ADD_COMPANY_MODAL})

export const closeAddCompanyModal = () => ({type: CLOSE_ADD_COMPANY_MODAL})

// ------------------------------------
// Reducer
// ------------------------------------
const initialState = {
  companies: [],
  selectedCompany: null,
  company: DEFAULTS.company,
  addCompanyModalOpened: false,
}

export default createReducer(initialState, {
  [CHANGE_COMPANY]: (state, {company}) => ({
    company,
  }),
  [ADD_COMPANY]: (state, {companies, selectedCompany}) => ({
    companies,
    selectedCompany,
  }),
  [REMOVE_COMPANY]: (state, {companies, selectedCompany}) => ({
    companies,
    selectedCompany,
  }),
  [SELECT_COMPANY]: (state, {selectedCompany, company}) => ({
    selectedCompany,
    company,
  }),
  [OPEN_ADD_COMPANY_MODAL]: (state, action) => ({
    addCompanyModalOpened: true,
  }),
  [CLOSE_ADD_COMPANY_MODAL]: (state, action) => ({
    addCompanyModalOpened: false,
  }),
})



