import React from 'react'
import {connect} from 'react-redux'
import withStyles from 'isomorphic-style-loader/lib/withStyles'
import s from './Details.css'
import {TinyMCE} from '../../components'
import {Col, Form, Icon, Row, Tooltip} from 'antd'
import {SIMPLE_TINYMCE_CONFIG} from '../../constants'
import format from 'date-fns/format'
import cn from 'classnames'

let uuid = 2

class Details extends React.Component {
  removeItem = (k) => {
    const keys = this.props.form.getFieldValue('keys')
    const newKeys = keys.filter(key => key !== k)
    this.props.form.setFieldsValue({keys: newKeys})
  }

  addItem = () => {
    const keys = this.props.form.getFieldValue('keys')
    const newKeys = keys.concat(uuid)
    uuid++
    this.props.form.setFieldsValue({keys: newKeys})
  }

  render() {
    const {defaultToday, dateFormat} = this.props
    const {getFieldDecorator, getFieldValue} = this.props.form
    this.props.form.getFieldDecorator('keys', {initialValue: [0, 1]})

    // TODO move
    const values = [
      {label: `Invoice Number`, value: `IN/1/2018`},
      {label: `Date`, value: defaultToday ? format(Date.now(), dateFormat) : ''},
    ]

    const keys = getFieldValue('keys')

    return (
      <div className={s.itemsWrapper}>
        {keys.map((k, i) =>
          <Row key={k} type='flex' gutter={16} className={s.item}>
            <Col span='12'>
              {getFieldDecorator(`details[${k}].label`, {
                initialValue: values[k] ? values[k].label : ``,
              })(
                <TinyMCE
                  className={cn(s.field, s.label)}
                  tinymceConfig={SIMPLE_TINYMCE_CONFIG}
                  component='div'
                />
              )}
            </Col>
            <Col span='12'>
              {getFieldDecorator(`details[${k}].value`, {
                initialValue: values[k] ? values[k].value : ``,
              })(
                <TinyMCE
                  className={s.field}
                  tinymceConfig={SIMPLE_TINYMCE_CONFIG}
                  component='div'
                />
              )}
            </Col>
            {i === keys.length - 1 && (
              <Tooltip placement='left' title={'Add row'}>
                <a onClick={this.addItem} className={s.addBtn}>
                  <Icon
                    className={s.addIcon}
                    type='plus'
                  />
                </a>
              </Tooltip>
            )}
            {i > 1 && (
              <Tooltip placement='right' title={'Remove row'}>
                <a onClick={() => this.removeItem(k)} className={s.removeBtn}>
                  <Icon
                    className={s.removeIcon}
                    type='minus'
                  />
                </a>
              </Tooltip>
            )}
          </Row>
        )}
      </div>
    )
  }
}

const mapState = state => ({
  defaultToday: state.settings.defaultToday,
  dateFormat: state.settings.dateFormat,
})

const mapDispatch = {}

export default connect(mapState, mapDispatch)(Form.create()(withStyles(s)(Details)))



