import createReducer from '../createReducer'
import {DEFAULTS} from '../constants'

// ------------------------------------
// Constants
// ------------------------------------
export const CHANGE_CLIENT = 'Client.CHANGE_CLIENT'
export const SELECT_CLIENT = 'Client.SELECT_CLIENT'
export const ADD_CLIENT = 'Client.ADD_CLIENT'
export const REMOVE_CLIENT = 'Client.REMOVE_CLIENT'

export const OPEN_ADD_CLIENT_MODAL = 'Client.OPEN_ADD_CLIENT_MODAL'
export const CLOSE_ADD_CLIENT_MODAL = 'Client.CLOSE_ADD_CLIENT_MODAL'

// ------------------------------------
// Actions
// ------------------------------------
export const changeClient = (client) => ({type: CHANGE_CLIENT, client})

export const addClient = ({label}) => (dispatch, getState) => {
  const {clients, client} = getState().client
  const newClient = {value: client, label}
  dispatch(closeAddClientModal())
  dispatch({
    type: ADD_CLIENT,
    clients: [...clients, newClient],
    selectedClient: newClient,
  })
}

export const removeClient = (clientToRemove) => (dispatch, getState) => {
  let {clients, selectedClient} = getState().client

  if (selectedClient && selectedClient.value === clientToRemove.value) {
    selectedClient = null
  }

  dispatch({
    type: REMOVE_CLIENT,
    clients: clients.filter(item => item.value !== clientToRemove.value),
    selectedClient,
  })
}

export const selectClient = (client) => (dispatch, getState) => {
  const {clients} = getState().client
  const selectedClient = clients.find(item => item.value === client)
  dispatch({type: SELECT_CLIENT, selectedClient, client})
}

export const openAddClientModal = () => ({type: OPEN_ADD_CLIENT_MODAL})

export const closeAddClientModal = () => ({type: CLOSE_ADD_CLIENT_MODAL})

// ------------------------------------
// Reducer
// ------------------------------------
const initialState = {
  clients: [],
  selectedClient: null,
  client: DEFAULTS.client,
  addClientModalOpened: false,
}

export default createReducer(initialState, {
  [CHANGE_CLIENT]: (state, {client}) => ({
    client,
  }),
  [ADD_CLIENT]: (state, {clients, selectedClient}) => ({
    clients,
    selectedClient,
  }),
  [REMOVE_CLIENT]: (state, {clients, selectedClient}) => ({
    clients,
    selectedClient,
  }),
  [SELECT_CLIENT]: (state, {selectedClient, client}) => ({
    selectedClient,
    client,
  }),
  [OPEN_ADD_CLIENT_MODAL]: (state, action) => ({
    addClientModalOpened: true,
  }),
  [CLOSE_ADD_CLIENT_MODAL]: (state, action) => ({
    addClientModalOpened: false,
  }),
})



