import React from 'react'
import {connect} from 'react-redux'
import withStyles from 'isomorphic-style-loader/lib/withStyles'
import s from './Items.css'
import {Label, TinyMCE, withCurrency} from '../../components'
import {numberWithCommas, parser} from '../../utils'
import isNaN from 'lodash/isNaN'
import sum from 'lodash/sum'
import {Col, Form, Icon, InputNumber, Row, Tooltip} from 'antd'
import {SIMPLE_TINYMCE_CONFIG} from '../../constants'
import cn from 'classnames'

let uuid = 1

class Items extends React.Component {
  removeItem = (k) => {
    const keys = this.props.form.getFieldValue('keys')
    const newKeys = keys.filter(key => key !== k)
    this.props.form.setFieldsValue({keys: newKeys})
  }

  addItem = () => {
    const keys = this.props.form.getFieldValue('keys')
    const newKeys = keys.concat(uuid)
    uuid++
    this.props.form.setFieldsValue({keys: newKeys})
  }

  getItemPrice = (k) => {
    const items = this.props.form.getFieldValue('items')
    const itemPrice = items[k].quantity * items[k].price
    return (!isNaN(itemPrice) ? itemPrice : 0).toFixed(2)
  }

  getSubTotalPrice = () => {
    // TODO fix issue with not changed price when removing item
    const items = this.props.form.getFieldValue('items')
    const subTotalPrice = sum(items.map(item => item.quantity * item.price))
    return !isNaN(subTotalPrice) ? subTotalPrice : 0
  }

  render() {
    const {taxVisible, tax, currency} = this.props
    const {getFieldDecorator, getFieldValue} = this.props.form
    const isTaxValid = !isNaN(+tax)
    this.props.form.getFieldDecorator('keys', {initialValue: [0]})

    const keys = getFieldValue('keys')

    return (
      <div className={s.itemsWrapper}>
        <Row className={s.itemsHeader}>
          <Col span='6'>
            {getFieldDecorator('itemLabel', {
              initialValue: `Item`,
            })(
              <TinyMCE
                className={cn(s.field, s.label)}
                tinymceConfig={SIMPLE_TINYMCE_CONFIG}
                component='div'
              />
            )}
          </Col>
          <Col span='6'>
            {getFieldDecorator('quantityLabel', {
              initialValue: `Quantity`,
            })(
              <TinyMCE
                className={cn(s.field, s.label)}
                tinymceConfig={SIMPLE_TINYMCE_CONFIG}
                component='div'
              />
            )}
          </Col>
          <Col span='6'>
            {getFieldDecorator('unitPriceLabel', {
              initialValue: `Unit Price`,
            })(
              <TinyMCE
                className={cn(s.field, s.label)}
                tinymceConfig={SIMPLE_TINYMCE_CONFIG}
                component='div'
              />
            )}
          </Col>
          <Col span='6'>
            {getFieldDecorator('amountLabel', {
              initialValue: `Amount`,
            })(
              <TinyMCE
                className={cn(s.field, s.label)}
                tinymceConfig={SIMPLE_TINYMCE_CONFIG}
                component='div'
              />
            )}
          </Col>
        </Row>
        {keys.map((k, i) =>
          <Row key={k} className={s.item}>
            <Col span='6'>
              {getFieldDecorator(`items[${k}].item`, {
                initialValue: `Product ${k + 1}`,
              })(
                <TinyMCE
                  className={s.field}
                  tinymceConfig={SIMPLE_TINYMCE_CONFIG}
                  component='div'
                />
              )}
            </Col>
            <Col span='6'>
              {getFieldDecorator(`items[${k}].quantity`, {
                initialValue: 1,
              })(
                <InputNumber
                  min={1}
                  precision={0}
                  className={s.quantity}
                  onFocus={(e) => {
                    e.target.select()
                  }}
                />
              )}
            </Col>
            <Col span='6'>
              {getFieldDecorator(`items[${k}].price`, {
                initialValue: 100,
              })(
                <Label
                  className={s.price}
                  min={0.01}
                  step={10}
                  precision={2}
                  formatter={numberWithCommas}
                  labelFormatter={(label) => withCurrency(currency, label)}
                  parser={parser}
                  component={InputNumber}
                />
              )}
            </Col>
            <Col span='6'>
              {withCurrency(currency, numberWithCommas((this.getItemPrice(k))))}
            </Col>
            {i === keys.length - 1 && (
              <Tooltip placement='left' title={'Add row'}>
                <a onClick={this.addItem} className={s.addBtn}>
                  <Icon
                    className={s.addIcon}
                    type='plus'
                  />
                </a>
              </Tooltip>
            )}
            {keys.length > 1 && (
              <Tooltip placement='right' title={'Remove row'}>
                <a onClick={() => this.removeItem(k)} className={s.removeBtn}>
                  <Icon
                    className={s.removeIcon}
                    type='minus'
                  />
                </a>
              </Tooltip>
            )}
          </Row>
        )}
        {taxVisible && (
          <Row className={s.itemsFooter}>
            <Col span='6' offset='12'>
              {getFieldDecorator('taxLabel', {
                initialValue: `VAT`
              })(
                <TinyMCE
                  className={cn(s.field, s.label)}
                  tinymceConfig={SIMPLE_TINYMCE_CONFIG}
                  component='div'
                />
              )}
            </Col>
            <Col span='6'>
              {isTaxValid ? withCurrency(currency, (this.getSubTotalPrice() * tax / 100).toFixed(2)) : tax}
            </Col>
          </Row>
        )}
        <Row className={s.itemsFooter}>
          <Col span='6' offset='12'>
            {getFieldDecorator('totalLabel', {
              initialValue: `Total Price`
            })(
              <TinyMCE
                className={cn(s.field, s.label)}
                tinymceConfig={SIMPLE_TINYMCE_CONFIG}
                component='div'
              />
            )}
          </Col>
          <Col span='6'>
            {withCurrency(currency, numberWithCommas((this.getSubTotalPrice() + (isTaxValid ? (this.getSubTotalPrice() * tax / 100) : 0)).toFixed(2)))}
          </Col>
        </Row>
      </div>
    )
  }
}

const mapState = state => ({
  currency: state.settings.currency,
  taxVisible: state.settings.taxVisible,
  tax: state.settings.tax,
})

const mapDispatch = {
}

export default connect(mapState, mapDispatch)(Form.create()(withStyles(s)(Items)))



