import React from 'react'
import {connect} from 'react-redux'
import withStyles from 'isomorphic-style-loader/lib/withStyles'
import s from './Company.css'
import {TinyMCE, AddCompanyModal} from '../../components'
import {Icon, Select, Tooltip} from 'antd'
import {TINYMCE_CONFIG} from '../../constants'
import isEmpty from 'lodash/isEmpty'
import {openAddCompanyModal, changeCompany, removeCompany, selectCompany} from '../../reducers/company'

class Company extends React.Component {
  render() {
    const {openAddCompanyModal, companies, selectedCompany, removeCompany, changeCompany, company, selectCompany, addCompanyModalOpened} = this.props
    return (
      <div className={s.companyWrapper}>
        <TinyMCE
          className={s.field}
          tinymceConfig={TINYMCE_CONFIG}
          component='div'
          value={company}
          onChange={changeCompany}
        />
        {!isEmpty(companies) && (
          <Select
            className={s.company}
            value={selectedCompany && selectedCompany.value}
            onChange={selectCompany}
          >
            {companies.map(item =>
              <Select.Option key={item.value} className={s.companyOption}>
                <div className={s.companyLabel}>{item.label}</div>
                <Icon
                  type='delete'
                  className={s.removeCompany}
                  title='Remove'
                  onClick={(e) => {
                    e.stopPropagation()
                    removeCompany(item)
                  }}
                />
              </Select.Option>
            )}
          </Select>
        )}
        {(!selectedCompany || company !== selectedCompany.value) && (
          <Tooltip placement='right' title={'Save company'}>
            <Icon
              className={s.saveIcon}
              onClick={openAddCompanyModal}
              type='save'
            />
          </Tooltip>
        )}
        {addCompanyModalOpened && <AddCompanyModal/>}
      </div>
    )
  }
}

const mapState = state => ({
  ...state.company,
})

const mapDispatch = {
  openAddCompanyModal,
  changeCompany,
  selectCompany,
  removeCompany,
}

export default connect(mapState, mapDispatch)(withStyles(s)(Company))




